
import CheckList from "./CheckList"

const PhaseCheckList = ({ section }) => {
    if (section.items) {
        return (
            <>
                <h4>{section.label} checklist items</h4>
                <CheckList steps={section.items} first={true} />
            </>
        )
    } else {
        return (
            <h4>There are no checklist items for this section</h4>
        )
    }
}

export default PhaseCheckList