import { useCallback, useContext, useEffect, useState } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import DoneIcon from "../icons/DoneIcon"

import Prompt from "./Prompt"

const arraysAreEqual = (arr1, arr2) => {
    let retVal = true
    if (arr1.length !== arr2.length) {
        retVal = false
    } else {
        arr1.forEach((val, index) => {
            if (arr2[index] !== val) {
                retVal = false
            }
        })
    }
    return retVal
}

const getPromptIDs = (children) => {
    const prompts = children.filter(child => child.type === "prompt")
    const promptIDs = prompts.map(child => child.id)
    // console.log(prompts, promptIDs)
    return promptIDs
}

const IntvForm = ({ id, intervention, label, checkedWhen, prompts }) => {
    const {
        evalIDExpression,
        getCheckListIDValues,
        checkListItems,
        isChecked,
        setChecked,
        setDerivedCheckListItemValue,
        currentInterventionForm
    } = useContext(ScenarioContext)

    // const [depIDs, setDepIDs] = useState(calcDepIDs)
    const [promptIDs, setPromptIDs] = useState([])
    // const [depIDsValues, setDepIDsValues] = useState(getCheckListIDValues(depIDs))
    const [promptValues, setPromptValues] = useState([])

    const calcPromptIDs = useCallback(() => getPromptIDs(prompts), [prompts])

    // const addIDs = (ids) => {
    //     console.log(`before: ${depIDs} adding: ${ids}`)
    //     const newIds = ids.filter(e => !depIDs.includes(e))
    //     setDepIDs(depIDs.concat(newIds))
    // }
    // const removeDepIDs = (ids) => {
    //     console.log(`before: ${depIDs} removing ${ids}`)
    //     const newIds = depIDs.filter(e => !ids.includes(e))
    //     setDepIDs(newIds)
    // }
    const computeValues = () => {
        const newVals = getCheckListIDValues(promptIDs)
        const oldVals = promptValues
        if (!arraysAreEqual(newVals, oldVals)) {
            setPromptValues(newVals)
        }
    }
    useEffect(
        () => {
            setPromptIDs(calcPromptIDs())
        }, [id, label, prompts]
    )
    useEffect(
        () => {
            // console.log(`IntvForm() id: ${id} checkedWhen: ${checkedWhen} depIDs: ${depIDs} depIDsValues: ${depIDsValues}`)
            // setDerivedCheckListItemValue(id, checkedWhen, depIDs)
            computeValues()
        },
        [checkListItems, promptIDs]
    )
    // set derived value whenever something changes
    useEffect(
        () => {
            // console.log()
            //
            if (getCheckListIDValues(promptIDs).every(val => val === true)) {
                setChecked(intervention, true)
            } else {
                setChecked(intervention, false)
            }
            // setDerivedCheckListItemValue(id, checkedWhen, depIDs)
        },
        [promptValues]
    )

    const shouldShowPrompt = (prompt) => {
        return (undefined == prompt.showConstraints) || (undefined !== prompt.showConstraints && evalIDExpression(prompt.showConstraints))
    }

    return (
        <div>
            {/* <pre>
                form: {id}<br/>
                depIds: {JSON.stringify(depIDs)}<br />
                depIdsValues: {JSON.stringify(depIDsValues)}<br />
                checkedWhen: {checkedWhen}<br />
            </pre> */}

            <span className="h4">
                <DoneIcon checked={isChecked(id)} />
                {label}
            </span>
            <hr className="minimal" />
            <ul>
                {prompts.map(prompt => (
                    shouldShowPrompt(prompt) && (
                    <li key={prompt.id}>
                        <Prompt {...prompt} />
                    </li>)
                ))}
            </ul>
        </div>
    )
}

export default IntvForm