import { useEffect, useState } from "react"

import {
    NavLink,
    Switch,
    useRouteMatch
 } from "react-router-dom"

import {
    Col,
    Container,
    Row
} from "reactstrap"

import { PrivateRoute } from "@meddbriefer/mdb-auth"

import UnauthedLayout from "./UnauthedLayout"
import { useScenario } from "../../contexts/ScenarioContext"
import { usePrefs } from "../../contexts/PreferencesContext"

import Phase     from "../../routes/Phase"
import HeadToToe from "../../routes/HeadToToe"

import SectionLink from "../SectionLink"


const PhaseDetailsLayout = () => {
    const { scenario } = useScenario()
    const { headToToeStyle } = usePrefs()
    const { path, url} = useRouteMatch()
    const [phases, setPhases] = useState([])

    useEffect(() => {
        if (headToToeStyle === "stick-figure") {
            setPhases(scenario.phaseData.filter(rec => rec.uiNav === true))
        } else {
            setPhases(scenario.phaseData.filter(rec => rec.dataEntry === true))
        }
    }, [headToToeStyle])

    return (
        <Container fluid={true} className="main">
            <Row>
                <Col md="2" className="col left-nav">
                    <ul style={{padding: 0}}>
                        {phases.map(rec => (
                           <li key={rec.id} className={rec.type}>
                               {(rec.type === "phase" && !!rec.subPhases)
                                    ? <b> {rec.label}</b>
                                    : <SectionLink to={`${url}/${rec.id}`}> {rec.label} </SectionLink>
                               }
                           </li>
                        ))}
                    </ul>
                </Col>
                <Col md="10" className="col phase-details">
                    <Switch>
                        <PrivateRoute
                            exact path={`${path}/head-to-toe`}
                            authLayout={UnauthedLayout}
                            component={HeadToToe}
                        />
                        <PrivateRoute
                            path={`${path}/:phaseID`}
                            authLayout={UnauthedLayout}
                            component={Phase}
                        />
                        <PrivateRoute
                            exact path={path}
                            authLayout={UnauthedLayout}
                        >
                            <h3>Please select a section on the left.</h3>
                        </PrivateRoute>
                    </Switch>
                </Col>
            </Row>
        </Container>
    )
}


export default PhaseDetailsLayout