const InterventionsList = ({ section }) => {
    const { setCurrIntvCat, setCurrIntv } = useScenario()
    const { intvListStyle } = usePrefs()

    useEffect(() => {
        /* clear the current intervention */
        setCurrIntv(null)
        /* if this section has a interventionsMenu (category) associated with it, set it */
        if (!!section.interventionsMenu && typeof section.interventionsMenu === "string" && "" !== section.interventionsMenu) {
            setCurrIntvCat(section.interventionsMenu)
        } else {
            setCurrIntvCat("")
        }
    }, [section])

    switch (intvListStyle) {
        case "accordian":
            return (<InterventionsAccordian />)
        case "dropdown":
            return (<InterventionsMenu />)
        case "vert-tabs":
            return (<VerticalInterventionTabs />)
        default:
            return (<InterventionTabs />)
    }
}

export default InterventionsList