import { useContext, useEffect } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import CheckmarkIcon from "../icons/CheckmarkIcon"

import CheckBoxGroup from "./CheckBoxGroup"
import RadioGroup from "./RadioGroup"

const Prompt = ({ id, label, selectionType, options }) => {
    const { isChecked } = useContext(ScenarioContext)

    useEffect(() => {
        console.log(id, label, selectionType)
    }, [])
    return (
        <>
            {"none" !== selectionType && <CheckmarkIcon checked={isChecked(id)} />}
            <i>{label}</i>
            {"one-or-more" === selectionType && <CheckBoxGroup id={id} options={options} />}
            {"one" === selectionType && <RadioGroup id={id} options={options} />}
        </>
    )
}

export default Prompt