import { useEffect, useState } from 'react';

import {ReactComponent as StickFigure } from './head2toe.svg';

import { useScenario } from '../contexts/ScenarioContext';

import PhaseCheckList from "./PhaseCheckList"


const PATHS = ["head", "neck", "clavicle", "shoulders", "chest", "abdomen-pelvis", "lower-extremities", "upper-extremities", "posterior"]

const StickFigureNav = () => {
  const { scenario } = useScenario()
  const [currentBodyPart, setCurrentBodyPart] = useState(null)
  const [subPhase, setSubPhase] = useState(null)

  const getPath = (event) => {
    let retVal = null;
    const pth = event.path.filter(item => PATHS.includes(item.id))
    if (pth && pth.length > 0) {
      retVal = pth[0]
    }
    return retVal
  }

  const setNotActive = () => {
    PATHS.forEach(id => {
      const el = document.getElementById(id)
      if (!!el) {
        el.classList.remove("active")
      }
    })
  }

  const clickHandler = (event) => {
    const clickedPath = getPath(event)
    setNotActive()
    if (!!clickedPath) {
      clickedPath.classList.add("active")
      // console.log(clickedPath)
      setCurrentBodyPart(clickedPath.id)
      // console.log(`you clicked on ${clickedPath.id}`)
    }
  }

  useEffect(() => {
    PATHS.forEach(id => {
      const el = document.getElementById(id)
      if (!!el) {
        el.addEventListener("click", clickHandler)
        el.classList.add("hoverable")
      } else {
        console.log(`ERROR: svg does not contain ID ${id}`)
      }
    })
    // cleanup
    return (() => {
      PATHS.forEach(id => {
        const el = document.getElementById(id)
        if (!!el) {
          el.removeEventListener("click", clickHandler)
        }
      })
    })
  // eslint-disable-next-line
  }, [])

  useEffect(() => {
    if (!!currentBodyPart) {
      const pd = scenario.phaseData.find(rec => rec.id === currentBodyPart)
      if (!!pd) {
        setSubPhase(pd)
      } else {
        setSubPhase(null)
      }
    } else {
      setSubPhase(null)
    }
  }, [currentBodyPart])
  return (
    <div class="stick-figure-nav">
      {/* width="210px" height="280px" */}
        <StickFigure  className="dev" />
        {!!subPhase
        ? <div><PhaseCheckList section={subPhase} /></div>
          : <h4>Click on a body part to assess it</h4>
        }
    </div>
  );
}

export default StickFigureNav;
