import React, { useContext } from "react"

import { ScenarioContext } from "../contexts/ScenarioContext"

import CheckBox from "./CheckBox"
import CallOutText from "./CallOutText"
import CallOutIcon from "./icons/CallOutIcon"

const CheckListItem = ({step}) => {
    const {
        isChecked,
        setCurrentInterventionForm,
        toggleChecked,
        toggleAssessmentFinding,

    } = useContext(ScenarioContext)

    switch (step.type) {
        case 'assessment':
            const inlines = (<CallOutIcon step={step} />)
            return (
                <CheckBox
                    step={step}
                    clickHandler={toggleAssessmentFinding}
                    inlineChildren={inlines}
                >
                    <div>
                        {isChecked(step.id) && <CallOutText step={step} /> }
                    </div>
                </CheckBox>
            )
        case 'intervention-form':
            return (
                <CheckBox
                    step={step}
                    clickHandler={() => setCurrentInterventionForm(step.id)}
                />
                //     {shouldDisplayInterventionForm(step.id) && (
                //         <InterventionForm form={step.id} />
                //     )}
                // </CheckBox>
            )
        case "prompt":
            return (<i>{step.label}</i>)
        default:
            // case 'required-action':
            // case 'critical-criteria':
            // case 'execution-error':
            return (
                <CheckBox
                    step={step}
                    clickHandler={toggleChecked}
                />
            )
        }

}


export default CheckListItem;